<template>
  <div id="new-employee">
    <div class="container">
      <h3>New employee component</h3>
      <form @submit.prevent="saveEmployee" class="col s12">
        <div class="row">
          <div class="input-field col s12">
            <input type="text" v-model="employee_id" required>
            <label>Employee id#</label>
          </div>
        </div>
        <div class="row">
          <div class="input-field col s12">
            <input type="text" v-model="name" required>
            <label>Name</label>
          </div>
        </div>
        <div class="row">
          <div class="input-field col s12">
            <input type="text" v-model="dept" required>
            <label>Dept</label>
          </div>
        </div>
        <div class="row">
          <div class="input-field col s12">
            <input type="text" v-model="position" required>
            <label>Position</label>
          </div>
        </div>
        <div class="row">
          <div class="input-field col s12">
            <router-link to="/">
              <button class="btn red">Cancel</button>
            </router-link>
            <input class="btn green" type="submit" placeholder="Add Employee">
          </div>
        </div>
      </form>
    </div>
  </div>
</template>


<script>
  import db from './firebaseInit'
  export default {
    name: "new-employee",
    data(){
      return{
        employee_id : null,
        name: null,
        dept: null,
        position: null
      }
    },
    methods:{
      saveEmployee(){
        db.collection('employees').add({
          employee_id : this.employee_id,
          name: this.name,
          dept: this.dept,
          position: this.position
        })
          .then(docRef =>{
            console.log('Document writen ID: ', docRef.id)
            this.$router.push('/')
          })
          .catch(error =>{
            alert('there was an error ', error)
          })
      }
    }
  }
</script>

<style scoped>

</style>



