<template>
  <div id="edit-employee">
    <div class="container">
      <h3>Edit employee</h3>
      <form @submit.prevent="saveEditEmployee" class="col s12">
        <div class="row">
          <div class="input-field col s12">
            <input type="text" v-model="employee_id" required>
          </div>
        </div>
        <div class="row">
          <div class="input-field col s12">
            <input type="text" v-model="name" required>
          </div>
        </div>
        <div class="row">
          <div class="input-field col s12">
            <input type="text" v-model="dept" required>
          </div>
        </div>
        <div class="row">
          <div class="input-field col s12">
            <input type="text" v-model="position" required>
          </div>
        </div>
        <div class="row">
          <div class="input-field col s12">
            <router-link to="/">
              <button class="btn red">Cancel</button>
            </router-link>
            <input class="btn green" type="submit" value="Save">
          </div>
        </div>
      </form>
    </div>
  </div>
</template>

<script>
  import db from './firebaseInit'

  export default {
    name: "edit-employee",

    data() {
      return {
        employee_id: null,
        name: null,
        dept: null,
        position: null
      }
    },
    beforeRouteEnter(to, from, next) {
      db.collection('employees').where('employee_id', '==', to.params.employee_id).get().then(querySnapshot => {
        querySnapshot.forEach(doc => {
          next(vm => {
              vm.employee_id = doc.data().employee_id,
              vm.name = doc.data().name,
              vm.dept = doc.data().dept,
              vm.position = doc.data().position
          })
        })
      })
    },

    watch: {
      '$route' : 'fetchData'
    },

    methods: {
      fetchData(){
        db.collection('employees').where('employee_id', '==', this.$route.params.id).get()
          .then(
            querySnapshot =>{
              querySnapshot.forEach(doc =>{
                this.employee_id = doc.data().employee_id
                this.name = doc.data().name
                this.dept = doc.data().dept
                this.position = doc.data().position
              })
            }
          )
      },
      saveEditEmployee() {


        this.$swal({
          title: 'Are you sure?',
          text: 'You will update the Emlpoyee!',
          type: 'warning',
          showCancelButton: true,
          confirmButtonText: 'Yes, update it!',
          cancelButtonText: 'No, keep it'
        }).then((result) => {
          if (result.value) {
            this.$swal(
              'Updated!',
              'Your employee has been updated.',
              'success'
            )
            db.collection('employees').where('employee_id', '==', this.$route.params.employee_id).get()
              .then(querySnapshot =>{
                querySnapshot.forEach(doc =>{

                  doc.ref.update({
                    employee_id : this.employee_id,
                    name: this.name,
                    dept: this.dept,
                    position: this.position
                  })
                    .then(res =>{
                      this.$router.push('/')
                    })
                })
              })
          } else if (result.dismiss === 'cancel') {
            this.$swal(
              'Cancelled',
              'Update has been canceled !',
              'error'
            )
          }
        })




      }
    }

  }
</script>

<style scoped>

</style>



