<template>
    <div class="dashboard">
      <div class="container">
        <ul class="collection width-header">
          <li class="collection-header">
            <h4>Employees</h4>
          </li>
          <li class="collection-item" v-for="employee in employees" :key="employee.id">
           <div class="chip">{{employee.dept}}</div>
            <i>
              {{employee.name}}
            </i>
            <router-link class="secondary-content" :to="{
              name: 'view-employee',
              params: {employee_id: employee.employee_id}
            }">
              <i class="fa fa-eye"></i>
            </router-link>
          </li>
        </ul>
      </div>
      <div class="fixed-action-btn">
        <router-link to="/new" class="btn-floating btn-large red">
          <i class="fa fa-plus"></i>
        </router-link>
      </div>
   </div>

</template>

<script>
  import db from './firebaseInit'
    export default {
        name: "dashboard",
      data(){
          return{
            employees: []
          }
      },

      created(){
        db.collection('employees').orderBy('name').get().then(querySnapshot => {
          querySnapshot.forEach(doc => {
            const data = {
              'id' : doc.id,
              'employee_id': doc.data().employee_id,
              'name':  doc.data().name,
              'dept':  doc.data().dept,
              'position':  doc.data().position,
            };
            this.employees.push(data);
          })
        })
      }
    }
</script>

<style scoped>

</style>



