<template>
    <div id="view-employee" class="container">
      <ul class="collection with-header">
        <li class="collection-header">
          <h4>{{name}}</h4>
        </li>
        <li class="collection-item">
          <i class="chip">Department:</i>{{dept}}
        </li>
      </ul>
      <router-link to="/">
        <a class="waves-effect waves-light btn-large">Back</a>
      </router-link>
      <a class="waves-effect waves-light btn-large red" @click="deleteUser">Delete</a>
      <div class="fixed-action-btn">

        <router-link :to="{name: 'edit-empoloyee', params: {employee_id: employee_id}}" class="btn-floating btn-large blue">
          <i class="fa fa-pencil"></i>
        </router-link>
      </div>
    </div>
</template>

<script>
  import db from './firebaseInit'
    export default {
        name: "view-employee",
      data(){
          return{
            employee_id : null,
            name: null,
            dept: null,
            position: null
          }
      },
      beforeRouteEnter(to, from, next){
          db.collection('employees').where('employee_id', '==', to.params.employee_id).get().then(querySnapshot => {
            querySnapshot.forEach(doc => {
              next(vm => {
                vm.employee_id = doc.data().employee_id,
                  vm.name = doc.data().name,
                  vm.dept = doc.data().dept,
                  vm.position = doc.data().position
              })
            })
          })
      },


      watch:{
          '$route': 'fetchData'
      },


      methods:{
          fetchData(){
            db.collection('employees').where('employee_id', '==', this.$route.params.employee_id).get().then(
              querySnapshot => {
                querySnapshot.forEach(doc =>{
                  this.employee_id = doc.data().employee_id,
                    this.name = doc.data().name,
                    this.dept = doc.data().dept
                })
              }
            )
          },
        deleteUser(){




              db.collection('employees').where('employee_id', '==', this.$route.params.employee_id).get()
                .then(querySnapshot =>{
                  querySnapshot.forEach(
                    doc =>{
                      doc.ref.delete()
                      this.$router.push('/')
                    }

                  )
                })
            }
      },

      created(){
          console.log( this.$router);
      }


    }
</script>

<style scoped>

</style>



